.artx-account-container {
  width: 272px;
  position: absolute;
  z-index: 10;
  top: 10%;
  right: 5%;
  background-color: white; }
  .artx-account-container button {
    cursor: pointer; }

.artx-gradient-text {
  background: -webkit-linear-gradient(352deg, #43d7a7, #ad5ef3);
  background: -o-linear-gradient(352deg, #43d7a7, #ad5ef3);
  background: linear-gradient(98deg, #43d7a7, #ad5ef3);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  border: 0; }



