.artx-bid-outter {
  width: 800px;
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  border-radius: 20px;
  padding: 5px;
  -webkit-box-shadow: 2px 2px 10px 5px rgba(73, 110, 105, 0.5), 2px 2px 10px 5px rgba(73, 110, 105, 0.5);
  box-shadow: 2px 2px 10px 5px rgba(73, 110, 105, 0.5), 2px 2px 10px 5px rgba(73, 110, 105, 0.5);
  background-image: -webkit-linear-gradient(336deg, #43d7a7, #ad5ef3);
  background-image: -o-linear-gradient(336deg, #43d7a7, #ad5ef3);
  background-image: linear-gradient(114deg, #43d7a7, #ad5ef3); }

.artx-progress-bar {
  background-color: #0e0525;
  border-radius: 0 15px 0 0;
  height: 20px;
  -webkit-transition: width 0.5s ease-in-out;
  -o-transition: width 0.5s ease-in-out;
  transition: width 0.5s ease-in-out; }

.artx-bid-inner {
  background-color: #0e0525;
  border-radius: 0 0 15px 15px; }
  .artx-bid-inner input {
    border-width: 0 0 3px 0;
    border-style: solid;
    border-color: white;
    background-color: transparent; }

.artx-icon-btn {
  border: 0;
  background-color: transparent; }

.artx-shares-btn button {
  border: 0;
  border-radius: 5px;
  background-image: -webkit-linear-gradient(338deg, #43d7a7, #ad5ef3);
  background-image: -o-linear-gradient(338deg, #43d7a7, #ad5ef3);
  background-image: linear-gradient(112deg, #43d7a7, #ad5ef3);
  padding: 2px; }
  .artx-shares-btn button > span {
    background-color: #0e0525;
    border-radius: 3px; }



